<template>
    <div class="app-user-page">
        <div class="app-user-title text-xs-center">
            <div class="user-avatar">
                <p>
                    <v-icon light class="user-avatar-icon">face</v-icon>
                </p>
                <p>User Name</p>
            </div>
            <v-list two-line>
                <v-list-item v-for="item in items" :key="item.title">
                    <v-list-tile avatar>
                        <v-list-tile-avatar>
                            <v-icon>{{ item.icon }}</v-icon>
                        </v-list-tile-avatar>
                        <v-list-tile-content>
                            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                            <v-list-tile-sub-title>{{ item.subtitle }}</v-list-tile-sub-title>
                        </v-list-tile-content>
                        <v-list-tile-action>
                            <span class="user-item-count" v-if="item.count">{{ item.count }}</span>
                        </v-list-tile-action>
                    </v-list-tile>
                </v-list-item>
            </v-list>
        </div>
    </div>
</template>

<script>
import {mapActions} from 'vuex';
import types from '@/store/mutation-types';
import pageLoadingMixin from '@/mixins/pageLoadingMixin';

export default {
    name: 'user',
    mixins: [pageLoadingMixin],
    data() {
        return {
            items: [
                {
                    title: 'Photos',
                    icon: 'photo_library',
                    subtitle: 'Jan 9, 2014'
                },
                {
                    title: 'Favorites',
                    icon: 'favorite',
                    subtitle: 'Feb 9, 2016'
                },
                {
                    title: 'Work',
                    icon: 'work',
                    subtitle: 'Nov 9, 2017'
                }
            ]
        };
    },
    methods: {
        ...mapActions([
            'setPageLoading',
            'setAppHeader',
            'showBottomNav',
            'activateBottomNav'
        ])
    },
    activated() {
        this.setAppHeader({
            show: true,
            title: 'VUE-PWA',
            showMenu: true,
            showBack: false,
            showLogo: true,
            actions: [
                {
                    icon: 'search',
                    route: '/search'
                }
            ]
        });
        // 设置当前 bottom navigator 显示的 item
        this.activateBottomNav('user');
        // 关闭加载中动画
        this.setPageLoading(false);
    },
    async mounted() {

        // 等待 1s，模拟异步请求
        await new Promise(resolve => {
            setTimeout(resolve, 1000);
        });

        // 设置模拟数据
        this.items[0].count = 6;
        this.items[1].count = 8;
        this.items[2].count = 2;

        // 触发强制渲染
        // this.$forceUpdate();
    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    
    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    
        $material-custom := {
            bg-color: #fff
            fg-color: #000
            text-color: #000
            primary-text-percent: 0.87
            secondary-text-percent: 0.54
            disabledORhints-text-percent: 0.38
            divider-percent: 0.12
            active-icon-percent: 0.54
            inactive-icon-percent: 0.38
        }
        $material-theme := $material-custom
    


.user-avatar
    color #333
    margin 50px auto 20px
    display flex
    justify-content center
    flex-direction column

    &-icon
        width 100px
        height 100px
        border-radius 100px
        background #666
        font-size 80px

.user-item-count
    height 24px
    width 24px
    border-radius 24px
    background #ccc
    font-size 14px
    line-height 24px
    color #fff
    font-weight bold
    vertical-align middle

</style>



